












import {
  defineComponent, reactive, computed, ref, toRefs,
} from '@vue/composition-api';

export default defineComponent({
  name: 'dialog-buttons',
  components: {},
  props: {
    confirm: {
      type: Function,
      default: async () => {},
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, {
    refs, emit, root, parent,
  }) {
    const state = reactive({});
    const cancel = () => {
      if (parent) parent.$emit('cancel');
    };

    return {
      ...toRefs(state),
      cancel,
    };
  },

});
